import React,{Component} from 'react';
import Input from '../../components/UI/Input/Input';
import Button from '../../components/UI/Button/Button';
import classes from './Auth.css';
import * as actions from '../../store/actions/index';
import {connect} from 'react-redux';
import Spinner from '../../components/UI/Spinner/Spinner';
import {Redirect} from 'react-router-dom';
import {updateObejct,checkValidity} from '../../shared/utility';

class Auth extends Component{
    state={
        controls:{
            email: {
                elementType:'input',
                elementConfig:{
                    type:'email',
                    placeholder:'Mail Address'
                },
                value:'',
                validation:{
                    required:true,
                    isEmail:true
                },
                valid:false,
                touched:false
            },
            password: {
                elementType:'input',
                elementConfig:{
                    type:'password',
                    placeholder:'Password'
                },
                value:'',
                validation:{
                    required:true,
                    minLength:6
                },
                valid:false,
                touched:false
            }
        },
        isSignup: true
    };

    componentDidMount(){
        //没有buildburger并且访问了该页面并且初始path不为‘/’
        if(!this.props.buildingBurger && this.props.authRedirectPath!=='/'){
            this.props.onSetAuthRedirectPath();  //将默认path改为'.'
        }
    };

    inputChangedHandler = (event, controlName)=>{
        const updatedControls = updateObejct(this.state.controls, {
            [controlName]:updateObejct(this.state.controls[controlName],{
               value:event.target.value,
                //使用检查函数
                valid:checkValidity(event.target.value, this.state.controls[controlName].validation),
                touched:true   //标记为输入过 
            })});
        this.setState({controls:updatedControls});
    }

    submitHandler = (event)=>{
        event.preventDefault();
        this.props.onAuth(this.state.controls.email.value,this.state.controls.password.value,this.state.isSignup);
        
    }

    switchAuthModeHandler = ()=>{
        this.setState(prevState=>{
            return {isSignup: !prevState.isSignup}
        })
    }

    render(){
        const formElementArray = [];
        for(let key in this.state.controls){     //遍历每个name
            formElementArray.push({
                id:key,
                config:this.state.controls[key]
            });
        };

        let form = formElementArray.map(formElement=>(
            <Input 
                key={formElement.id}
                elementType={formElement.config.elementType} 
                elementConfig={formElement.config.elementConfig} 
                value={formElement.config.value}
                changed={(event)=>this.inputChangedHandler(event,formElement.id)}
                invalid={!formElement.config.valid}
                shouldValidate={formElement.config.validation&& formElement.config.touched}
            />
        ));

        if(this.props.loading){
            form=<Spinner/>;
        }

        let errorMessage = null;
        if(this.props.error){
            errorMessage = (
                <p>{this.props.error.message}</p>   //从firebase获取的error
            )
        };

        let authRedirect = null;
        if(this.props.isAuthenticated){
            authRedirect=<Redirect to={this.props.authRedirectPath}/>
            //登录后app.js中没有return auth.js，所以不会执行此redirect
        };

        return(
            <div className={classes.Auth}>
                {authRedirect}
                {errorMessage}
                <form onSubmit={this.submitHandler}>
                    {form}
                    <Button btnType="Success">SUBMIT</Button>
                </form>
                <Button 
                    clicked={this.switchAuthModeHandler}
                    btnType="Danger">SWITCH TO {this.state.isSignup? 'SIGNIN':"SIGNUP"}</Button>
            </div>
        );
    };
};

//需要得到reducer中的state
//进行signup & in时都会改变loading，调用loading即可
const mapStateToProps = state=>{
    return {
        loading: state.auth.loading,
        error: state.auth.error,
        isAuthenticated: state.auth.token !== null,
        buildingBurger: state.burgerBuilder.building,
        authRedirectPath: state.auth.authRedirectPath
    };
};

const mapDispatchToPoprs =dispatch=>{
    return {
        onAuth:(email,password,isSignup)=>dispatch(actions.auth(email,password,isSignup)),
        onSetAuthRedirectPath:()=>dispatch(actions.setAuthRedirectPath('/'))
        //将state中的path还原为主界面
    };
};


export default connect(mapStateToProps,mapDispatchToPoprs)(Auth);


